import React from 'react'
import { NavLink } from 'react-router-dom'
import { incrementByOne, incrementByTwo, selectCount } from './todoSlice'
import { useDispatch } from 'react-redux/es/exports'
import { useSelector } from 'react-redux/es/hooks/useSelector'

export default function Counter() {
    const count = useSelector(selectCount)
    const dispatch=useDispatch()
  return (
    <div>
         <NavLink to='/todos'>View All Todos</NavLink><br />
        <button style={{'marginRight':'15px'}}  onClick={()=>dispatch(incrementByOne(1))}> +1 </button> 
        {count}
        <button style={{'marginLeft':'15px'}}onClick={()=>dispatch(incrementByTwo(2))}> +2 </button>
    </div>
  )
}
