import axios from 'axios'
import React, { useState } from 'react'
import {NavLink} from 'react-router-dom'
import { useNavigate } from 'react-router'
import { useDispatch } from 'react-redux'
import { addTodo } from './todoSlice'

export default function AddTodo() {
  const[todoForm,updateTodoForm]=useState({
  description: '',
      completed: false
  })
  const navigate =useNavigate()
  const dispatch  = useDispatch()
  const handleSubmit=async(e)=>
  {
    e.preventDefault()
    dispatch(addTodo({
      ...todoForm
    }))
    navigate('/todos')
  }

  const handleChange=(e)=>
  {
    updateTodoForm(
      {
        ...todoForm,
        [e.target.name]:e.target.value
      }
    )
  }
  return (
    <div>
    <div>
    <NavLink to='/todos'>View All Todos</NavLink>
    </div>
    <form onSubmit={handleSubmit}>
      <label htmlFor="description">Task :</label>
      <input type="text" name="description" id="description" value={todoForm.description} onChange={handleChange}/>
      <input type="hidden" name="completed" id="completed"  value={todoForm.completed}/>
      <button>Add todo</button>
    </form>

    </div>
  )
}
