import { createAsyncThunk, createSlice } from "@reduxjs/toolkit" 
import axios from 'axios'

export const fetchTodos=createAsyncThunk('todos/fetchTodos',async()=>{
    const responsee = await axios.get('http://localhost:3000/todos')
    return responsee.data
})

export const addTodo=createAsyncThunk('todos/addTodo',async(postData)=>{
    const responsee = await axios.post('http://localhost:3000/todos',postData)
    return responsee.data
})

export const updateTodo=createAsyncThunk('todos/updateTodo',async({todoID,todoObj})=>{
    const responsee = await axios.patch(`http://localhost:3000/todos/${todoID}`,todoObj)
    return responsee.data
})

export const deleteTodo=createAsyncThunk('todos/deleteTodo',async(todoID)=>{
    await axios.delete(`http://localhost:3000/todos/${todoID}`)
    return todoID
})

export const todoSlice = createSlice({
    name:'todo',
    initialState:{
        count:0,
        todoItems:[],
        todoLoadingStatus:'idle',
        todoErrorStatus:null
    },
    reducers:{
        incrementByOne:(state,action)=>{
            return{
                ...state,
                count:state.count+action.payload
            }
        },
        incrementByTwo:(state,action)=>{
            return{
                ...state,
                count:state.count+action.payload
            }
        },
    },
    extraReducers(builder){
        builder.addCase(fetchTodos.pending,(state,action)=>
        {
            return{...state,todoLoadingStatus:"loading"}
        });
        builder.addCase(fetchTodos.fulfilled,(state,action)=>
        {
            return{...state,todoLoadingStatus:"success",todoItems:action.payload}
        });
        builder.addCase(fetchTodos.rejected,(state,action)=>
        {
            return{...state,todoLoadingStatus:"failed",todoErrorStatus:action.error.message}
        })
//////////////////////////////////////
        builder.addCase(addTodo.fulfilled,(state,action)=>
        {
            return{...state,todoLoadingStatus:"success",todoItems:[
                ...state.todoItems,
                action.payload
            ]}
        });
        builder.addCase(updateTodo.fulfilled,(state,action)=>
        {
            return{...state,todoLoadingStatus:"success",todoItems:state.todoItems.map(
                todoitem =>{
                    if(todoitem.id==action.payload.id)
                    {
                        return action.payload
                    }
                    return todoitem

                }
            )}
        });
        builder.addCase(deleteTodo.fulfilled,(state,action)=>
        {
            return{...state,todoLoadingStatus:"success",todoItems:
            state.todoItems.filter(todoitem => todoitem.id != action.payload)}
        });

    }
})

//exporting actiion to be used by another components
export const {incrementByOne,incrementByTwo} = todoSlice.actions

//exporting count as a selector
export const selectCount = state=>state.todos.count
export const selectTodos = state=>state.todos.todoItems
export const selectError = state=>state.todos.todoErrorStatus
export const selectLoading = state=>state.todos.todoLoadingStatus
export const selectTodo = (state,todoId)=>state.todos.todoItems.find(todoItem =>todoItem.id == todoId )


//exporting reducer
export default todoSlice.reducer