import { wait } from '@testing-library/user-event/dist/utils'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router'
import {NavLink} from 'react-router-dom'
import { useSelector,useDispatch } from 'react-redux'
import { deleteTodo, fetchTodos, selectError, selectLoading, selectTodos, updateTodo } from './todoSlice'

export default function AllTodos() {
    const navigate=useNavigate()
    const todos= useSelector(selectTodos)
    const isloading=useSelector(selectLoading)
    const error=useSelector(selectError)

    const dispatch = useDispatch()
    useEffect(()=>{
        if(isloading==='idle')
        {
            dispatch(fetchTodos())
        }
    },[isloading,dispatch])

    const updateTask=async(e,todo)=>{
         e.stopPropagation()
       dispatch(updateTodo({
        todoID:todo.id,
        todoObj:{...todo,completed:!todo.completed}
       }))}
    const deleteTask=async(e,todoId)=>{
        e.stopPropagation()
       dispatch(deleteTodo(todoId))
   }

  return (
    
    <div style={{'display':'flex','flexDirection':'column','alignItems':'center'}}>
        {error && <div>{error}</div>}
        {(isloading!="success") && <div>Loading Todos....</div>}

        <NavLink to='/add-todo'>Add New</NavLink>
        <NavLink to='/counter'>Show Counter Componet</NavLink>
        
        <div>
            {todos && todos.map(todo=>(
                
                 <div key={todo.id}  style={{'border':'2px solid red','textAlign':'center','padding':'5px 50px'}}>
                    
                    {todo.completed? 
                    <strike>
                        <h2  onClick={()=>navigate(`/todos/${todo.id}`)}>{todo.description}</h2>
                    </strike>:
                    <h2  onClick={()=>navigate(`/todos/${todo.id}`)}>{todo.description}</h2>}
                    
                    <button onClick={(e)=>updateTask(e,todo)}>
                       {todo.completed? 'Not Done':'Mark as Done'}
                    </button>
                    <button style={{'marginLeft':'10px'}} onClick={(e)=>deleteTask(e,todo.id)}>
                       Delete Task
                    </button>
                 </div>
                
            ))}
        </div>
    </div>
  )
}
